
.header {
  position: relative;
  overflow: hidden;
  color: #fff;
  background-image: url(./bg01.jpg);
  background-size: 100%, 100px;
  background-repeat: no-repeat;
  width: 100%;
  height: 100px;
}
.time {
  float: left;
  width: 60%;
  margin-top: 5px;
}
.time span {
  display: block;
  text-align: left;
  font-size: 20px;
  padding-top: 15px;
  padding-left: 20px;
}
.content-wrapper {
  float: right;
  width: 37%;
}



